import React, { useState } from "react";
import io from 'socket.io-client'
import { faArrowLeft, faArrowRight, faBullhorn, faLightbulb, } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import './App.css';
const ENDPOINT = "http://localhost:3000";
const socket = io.connect(ENDPOINT)


function App() {

  const [headlights, setHeadlights] = useState(false)
  const [blinkLeft, setBlinkLeft] = useState(false)
  const [blinkRight, setBlinkRight] = useState(false)
  const [horn, setHorn] = useState(false)

  socket.emit("headlights", headlights)
  socket.emit("blinkleft", blinkLeft)
  socket.emit("blinkright", blinkRight)
  socket.emit("horn", horn)

  const onHeadlightChange = () => {
    setHeadlights(!headlights)

    socket.emit("headlights", headlights)
  }

  const onBlinkLeftChange = () => {
    setBlinkLeft(!blinkLeft)

    if(blinkRight) {
      setBlinkRight(false)
    }

    socket.emit("blinkleft", blinkLeft)
    socket.emit("blinkright", blinkRight)
  }

  const onBlinkRightChange = () => {
    setBlinkRight(!blinkRight)

    if(blinkLeft) {
      setBlinkLeft(false)
    }
    
    socket.emit("blinkright", blinkRight)
    socket.emit("blinkleft", blinkLeft)
  }

  const onHornChange = () => {
    setHorn(!horn)
    
    socket.emit("horn", horn)
  }

  return (
    <div className="App">
      <button className="button" onClick={onHeadlightChange}>
      <FontAwesomeIcon className={headlights ? "yellow" : "blue"} icon={faLightbulb} size = '6x' />
      </button>
      <button className="button" onClick={onBlinkLeftChange}>
      <FontAwesomeIcon className={blinkLeft ? "green" : ""} icon={faArrowLeft} size = '6x' />
      </button>
      <button className="button" onClick={onBlinkRightChange}>
      <FontAwesomeIcon className={blinkRight ? "green" : ""} icon={faArrowRight} size = '6x' />
      </button>
      <button className="button" onClick={onHornChange}>
        <FontAwesomeIcon className={horn ? "red" : ""} icon={faBullhorn} size = '6x' />
      </button>
    </div>
  );
}

export default App;
